@import "colors";

@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,700&display=swap');

body {
  font-family: Ubuntu;
  background-color: $bgMain;
  margin: 0;
  color: white;
}

.nav {
  background-color: $bgDark;
  width: 100%;
  height: 5vh;
  overflow-x: auto;
  ul {
    list-style-type: none;
    background-color: $bgDark;
    margin: 0;
    user-select: none;
  }
  li {
    float: left;
    background-color: $bgDark;
    list-style-type: none;
    transition: background-color .3s;
    line-height: 5vh;
    padding-right: 10px;
    padding-left: 10px;
    font-size: large;
    font-weight: 500;
    cursor: pointer;
    &.spacer {
      cursor: auto;
      &:hover {
        background-color: inherit;
      }
    }
    &:hover, &.active {
      background-color: $bgMain;
    }
  }
}


